<template>
  <div class="home">
    <div
      v-loading="loading"
      element-loading-text="..."
      style="min-height: 35vw"
      v-if="!error"
    >
      <div class="break_news">
        <div class="news_img">
          <div class="news_img_title">
            <img class="news_img_icon" src="../../assets/img/shape.png" />
            <div class="news_img_content"></div>
          </div>
          <img class="news_img_main" src="../../assets/img/pg1.png" />
        </div>
        <div class="news_list">
          <div class="news_list_more">
            <img
              class="news_list_icon"
              src="../../assets/img/blueright.png"
            />
          </div>
          <div class="news_list_show" v-for="(item, i) of 7" :key="i">
            <img class="list_show_icon" src="../../assets/img/group.png" />
            <div class="list_show_content">
              :
            </div>
            <div class="list_show_date">2020-05-27</div>
          </div>
        </div>
      </div>
      <div class="news_intro">
        <div class="news_intro_show" v-for="(item, i) of 2" :key="i">
          <div class="news_intro_title">
            <img class="news_intro_txt_img" src="../../assets/img/backgg.png" />
            <div class="news_intro_txt"></div>
            <div class="news_list_more">
              <img
                class="news_list_icon"
                src="../../assets/img/blueright.png"
              />
            </div>
          </div>
          <div class="news_intro_content" v-for="(item, i) of 3" :key="i">
            <img class="intro_content_img" src="../../assets/img/pp.png" />
            <div class="intro_content">
              <div class="intro_content_title">
                
              </div>
              <div class="intro_content_txt">
                
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="notice">
        <div class="notice_title"><span></span></div>
        <div class="notice_content">
          <div
            class="notice_list"
            :class="showNotice == i ? 'notice_over' : ''"
            v-for="(item, i) of 7"
            :key="i"
            @mouseover="overNotice(i)"
            @mouseleave="leaveNotice(i)"
          >
            <div :class="showNotice == i ? 'notice_left' : ''"></div>
            <img class="notice_icon" src="../../assets/img/group.png" />
            <div class="notice_list_txt">
              
              201993
            </div>
            <div class="notice_list_date">2020-05-27</div>
          </div>
          <div class="news_list_more">
            <img
              class="news_list_icon"
              src="../../assets/img/blueright.png"
            />
          </div>
        </div>
      </div>
      <div class="icon_show">
        <div class="icon_show_title">
          <img class="news_intro_txt_img" src="../../assets/img/backgg.png" />
          <div class="icon_show_title_txt"></div>
        </div>
        <div class="icon_show_info">
          <div class="icon_show_content" v-for="(item, i) of 6" :key="i">
            <img class="icon_show_icon" src="../../assets/img/pp.png" />
            <div class="icon_show_titlee"></div>
            <div class="icon_show_txt">
              
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="no-info" v-if="error">
      <div class="no-data">
        <img src="/static/images/error.png" />
        <br />
        ...
      </div>
    </div>
  </div>
</template>
<script>
import { userGet } from "/api/user.js";

export default {
  data() {
    return {
      error: false,
      loading: false,
      showNotice: 99,
    };
  },
  methods: {
    overNotice(i) {
      this.showNotice = i;
    },
    leaveNotice(i) {
      this.showNotice = 99;
    },
  },
  mounted() {},
  created() {},
  components: {},
};
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
.home {
  display: flex;
  flex-direction: column;
}
.no-info {
  padding: 100px 0;
  text-align: center;
  font-size: 30px;
  display: flex;
  flex-direction: column;
  .no-data {
    align-self: center;
  }
}
.break_news {
  width: 1440px;
  margin: 24px auto;
  display: flex;
  .news_img {
    width: 730px;
    padding-left: 120px;
    .news_img_title {
      display: flex;
      .news_img_icon {
        width: 40px;
        height: 40px;
      }
      .news_img_content {
        font-size: 24px;
        color: #0593d2;
        margin-left: 8px;
        height: 40px;
        line-height: 40px;
        font-weight: bold;
      }
    }
    .news_img_main {
      width: 588px;
      height: 321px;
      margin-top: 12px;
    }
  }
  .news_list {
    width: 588px;
    .news_list_more {
      font-size: 16px;
      color: #0494d2;
      line-height: 40px;
      line-height: 40px;
      text-align: right;
      margin-bottom: 10px;
      .news_list_icon {
        margin-left: 15px;
      }
    }
    .news_list_show:hover {
      color: #0593d2;
    }
    .news_list_show {
      width: 588px;
      height: 46px;
      line-height: 46px;
      font-size: 16px;
      color: #333333;
      display: flex;
      border-bottom: 1px dashed #cccccc;
      cursor: pointer;
      .list_show_icon {
        width: 6px;
        height: 12px;
        position: relative;
        top: 16px;
      }
      .list_show_content {
        margin-left: 16px;
        width: 450px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      .list_show_date {
        margin-left: 20px;
        width: 100px;
        text-align: right;
      }
    }
  }
}
.news_intro {
  width: 1440px;
  margin: 0 auto;
  padding: 0 120px;
  display: flex;
  justify-content: space-between;
  height: auto;
  .news_intro_show {
    width: 588px;
    .news_intro_title {
      display: flex;
      justify-content: space-between;
      height: 44px;
      line-height: 44px;
      position: relative;
      z-index: 1;
      .news_intro_txt_img {
        position: absolute;
        left: 0;
        top: 0;
        width: 588px;
        height: 44px;
        z-index: 0;
      }
      .news_intro_txt {
        font-weight: bold;
        font-size: 20px;
        color: #ffffff;
        width: 160px;
        padding-left: 24px;
        position: relative;
        z-index: 2;
      }
      .news_list_more {
        font-size: 16px;
        color: #0494d2;
        line-height: 40px;
        line-height: 40px;
        text-align: right;
        margin-bottom: 10px;
        cursor: pointer;
        .news_list_icon {
          margin-left: 15px;
        }
      }
    }
    .news_intro_content {
      margin-top: 24px;
      display: flex;
      .intro_content_img {
        width: 120px;
        height: 120px;
      }
      .intro_content {
        width: 468px;
        height: 120px;
        border: 1px solid #ededed;
        border-radius: 2px;
        border-radius: 2px;
        padding: 12px;
        .intro_content_title {
          font-size: 18px;
          color: rgba(0, 0, 0, 0.85);
          height: 25px;
          line-height: 25px;
        }
        .intro_content_txt {
          font-size: 12px;
          color: rgba(0, 0, 0, 0.45);
          line-height: 17px;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 4;
          -webkit-box-orient: vertical;
          margin-top: 4px;
        }
      }
    }
  }
}
.icon_show {
  width: 1440px;
  margin: 24px auto;
  padding: 0 120px;
  .icon_show_title {
    width: 160px;
    height: 44px;
    position: relative;
    .news_intro_txt_img {
      position: absolute;
      left: 0;
      top: 0;
      width: 588px;
      height: 44px;
      z-index: 0;
    }
    .icon_show_title_txt {
      font-weight: bold;
      font-size: 20px;
      color: #ffffff;
      width: 160px;
      line-height: 44px;
      position: relative;
      padding-left: 24px;
    }
  }
  .icon_show_info {
    border-top: 3px solid #0593d2;
    padding-top: 26px;
    display: flex;
    justify-content: space-between;
    position: relative;
    left: 0;
    top: -2px;
    .icon_show_content {
      width: 178px;
      height: 200px;
      background: #e5f7ff;
      border-radius: 2px;
      border-radius: 2px;
      .icon_show_icon {
        width: 41px;
        height: 52px;
        margin: 24px 68px;
      }
      .icon_show_titlee {
        font-size: 16px;
        color: #333333;
        text-align: center;
        width: 100%;
        height: 22px;
        line-height: 22px;
      }
      .icon_show_txt {
        font-size: 12px;
        color: #787878;
        text-align: center;
        width: 100%;
        line-height: 17px;
        margin-top: 8px;
        padding: 0 12px;
      }
    }
  }
}
.notice {
  width: 1440px;
  height: 423px;
  background: #e5f7ff;
  border-radius: 2px;
  border-radius: 2px;
  margin: 24px auto 0;
  display: flex;
  .notice_title {
    width: 48px;
    height: 375px;
    margin: 24px 22px 24px 120px;
    background: #0593d2;
    border-radius: 2px;
    font-size: 25px;
    color: #ffffff;
    font-weight: bold;
    text-align: center;
    vertical-align: bottom;
    span {
      position: relative;
      top: 225px;
    }
  }
  .notice_content {
    width: 1127px;
    height: 375px;
    background: #ffffff;
    border-radius: 2px;
    border-radius: 2px;
    margin-top: 24px;
    .notice_list {
      width: 1090px;
      border-bottom: 1px dashed #dadada;
      height: 46px;
      line-height: 46px;
      display: flex;
      padding-left: 13px;
      color: #333333;
      position: relative;
      cursor: pointer;
      .notice_left {
        position: absolute;
        top: 10px;
        left: -22px;
        width: 0;
        height: 0;
        border: 14px solid;
        border-color: transparent transparent transparent #0593d2;
      }
      .notice_icon {
        width: 5px;
        height: 13px;
        position: relative;
        top: 16px;
      }
      .notice_list_txt {
        margin-left: 17px;
        font-size: 16px;
        width: 860px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      .notice_list_date {
        font-size: 16px;
        text-align: right;
        width: 200px;
      }
    }
    .notice_over {
      color: #0494d2;
    }
    .news_list_more {
      font-size: 16px;
      color: #0494d2;
      line-height: 40px;
      text-align: right;
      margin-right: 38px;
      cursor: pointer;
      .news_list_icon {
        margin-left: 15px;
      }
    }
  }
}
</style>



